import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { Icon } from '@cetus/ui-kit'
import { IconProps } from '@cetus/ui-kit/src/components/Icon'
import { Center, StackProps, VStack } from '@chakra-ui/react'
import { omit } from 'lodash-es'
import { useMemo } from 'react'
import LockInput, { LockType } from './LockInput'
import TradeInput, { TradeInputProps } from './TradeInput'

export interface TradeInputGroupProps {
  from: Omit<TradeInputProps, 'position'>
  to: Omit<TradeInputProps, 'position'>
  borderRadius?: string
  onClick?: () => void
  iconParams?: IconProps
  iconHover?: boolean
  iconShadow?: boolean
  wrapStyle?: StackProps
  currentCoinKey?: string
  whiteTokenList?: Token[]
  lock?: LockType
}

function TradeInputGroup({
  from,
  to,
  onClick = () => {},
  iconParams,
  borderRadius = '20px',
  wrapStyle = {},
  currentCoinKey,
  whiteTokenList,
  iconHover = true,
  iconShadow = false,
  lock
}: TradeInputGroupProps) {
  const hasHideInput = useMemo(() => {
    return from.hideSelf || to.hideSelf
  }, [from.hideSelf, to.hideSelf])

  const { isApp } = useWindowWidth()
  return (
    <VStack gap="8px" w="100%" position="relative" {...wrapStyle}>
      <TradeInput currentCoinKey={currentCoinKey} whiteTokenList={whiteTokenList} {...from} />
      <TradeInput currentCoinKey={currentCoinKey} whiteTokenList={whiteTokenList} {...to} />
      {iconParams && !hasHideInput && (
        <Center
          zIndex={999}
          w="36px"
          h="36px"
          borderRadius="50%"
          position="absolute"
          top={to.isDcaTo ? 'calc(50% - 4px)' : from.isWidget ? 'calc(50% - 20px)' : 'calc(50% - 18px)'}
          left="calc(50% - 18px)"
          border="1px solid"
          borderColor="token_inactive_border"
          boxShadow={iconShadow ? 'trade_icon_shadow' : 'unset'}
          bg={from?.isWidget ? 'swap_bg_secondary' : 'input_bg'}
          onClick={onClick}
          onMouseEnter={isApp ? () => {} : iconParams?.onMouseEnter}
          onMouseLeave={isApp ? () => {} : iconParams?.onMouseLeave}
          sx={{
            _hover:
              !iconHover || isApp
                ? {}
                : {
                    cursor: 'pointer',
                    borderColor: 'token_active_border',
                    boxShadow: '0px 0px 6px 0px #0067AD'
                  }
          }}
        >
          <Icon {...omit(iconParams, ['onMouseEnter', 'onMouseLeave'])} />
        </Center>
      )}
      {((typeof lock === 'boolean' && lock) || (typeof lock === 'object' && lock?.isLock)) && (
        <LockInput
          position="absolute"
          h={wrapStyle?.h || '224px'}
          top="0px"
          left="0px"
          zIndex={9999}
          borderRadius="16px"
          w="100%"
          text={typeof lock === 'object' ? lock?.text : undefined}
          {...(typeof lock === 'object' ? lock?.style || {} : {})}
        />
      )}
    </VStack>
  )
}

export default TradeInputGroup
